import React, { useEffect, useState } from "react";
import axios from "../../axios";
import {
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Table,
} from "@mui/material";
import { useSelector } from "react-redux";
function ListSchoolFaculty(props) {
  const collegeID = useSelector((state) => state.collegeID);

  const [rows, setRows] = useState([]);
  useEffect(() => {
    axios
      .get("api/departments/faculty/" + collegeID, {})
      .then((res) => {
        setRows(res.data);
      })
      .catch((error) => {
        alert(error);
        console.log(error);
      });
  }, [props.change]);
  return (
    <TableContainer component={Paper}>
      <Table aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell
              style={{ fontWeight: "bold", textTransform: "uppercase" }}
            >
              S.No
            </TableCell>
            <TableCell
              style={{ fontWeight: "bold", textTransform: "uppercase" }}
            >
              Faculty / School Name
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, index) => (
            <TableRow key={row.Faculty_ID}>
              <TableCell align="left">{index + 1}</TableCell>
              <TableCell align="left">{row.Faculty_Name}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default ListSchoolFaculty;
