import React, { useState } from "react";
import { useSelector } from "react-redux";
import * as Yup from "yup";
import axios from "../../axios";
import AddBoxOutlinedIcon from "@mui/icons-material/AddBoxOutlined";
import { Alert, Button, LinearProgress, Stack, Snackbar } from "@mui/material";
import { Formik, Form, Field } from "formik";
import { TextField } from "formik-material-ui";
import ListSchoolFaculty from "./listSchoolFaculty";

export default function AddSchoolFaculty() {
  const collegeID = useSelector((state) => state.collegeID);
  const [change, setChange] = useState();
  const [open, setOpen] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };
  return (
    <Stack sx={{ width: "100%" }} spacing={2}>
      <div className="p-2">
        <h4>
         <AddBoxOutlinedIcon className="mr-2"/>Add New Faculty / School
        </h4>
        <Formik
          initialValues={{
            Faculty_Name: "",
          }}
          validationSchema={Yup.object({
            Faculty_Name: Yup.string()
              .max(30, "Must be 30 characters or less")
              .min(3, "Must be at least 3 characters")
              .required("School/Faculty Name can't be Empty."),
          })}
          onSubmit={(values, { setSubmitting, resetForm }) => {
            setTimeout(() => {
              setSubmitting(false);
              axios
                .post(
                  "api/departments/faculty/" + collegeID,
                  JSON.stringify(values),
                  {
                    headers: {
                      // Overwrite Axios's automatically set Content-Type
                      "Content-Type": "application/json",
                      Accept: "application/json",
                    },
                  }
                )
                .then((res) => {
                  if (res.status === 200) {
                    setOpen(true);
                    setChange(res.data.resultID);
                  }
                })
                .catch(function (error) {
                  alert("Error occured:" + error);
                });
              resetForm();
            }, 500);
          }}
        >
          {({ submitForm, isSubmitting, values }) => (
            <Form>
              <Snackbar
                anchorOrigin={{ vertical: "top", horizontal: "center" }}
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
              >
                <Alert
                  onClose={handleClose}
                  severity="info"
                  sx={{ width: "100%" }}
                >
                  Faculty/School Added Successfully!
                </Alert>
              </Snackbar>
              <Field
                style={{
                  backgroundColor: "#f8f8f8",
                }}
                component={TextField}
                name="Faculty_Name"
                type="text"
                label="Faculty Name"
                variant="outlined"
                fullWidth
              />

              {isSubmitting && <LinearProgress />}
              <Button
                variant="contained"
                color="primary"
                className="mt-3"
                disabled={isSubmitting}
                onClick={submitForm}
              >
                Add New School
              </Button>
            </Form>
          )}
        </Formik>
      </div>
      <ListSchoolFaculty change={change} />
    </Stack>
  );
}
